html{
    background: #F7FBFE;
}


*{
    box-sizing: border-box;
    font-family:'Inter' !important;
    font-style: normal;
    font-weight: 400;
}

.button{
    border-radius: 18px;
    background: 0;
    border: 0;
    font-weight: 500;
}
a{
    transition: .3s;

    &:hover{
        color:  #02569C;
    }
}

.wrapper{
    max-width: 1280px;
    margin: 0 auto;
}

#header{
    width: 100vw;
    margin-bottom: 50px;
    background: white;
    box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);

    .wrapper{
        display: flex;
        align-items: center;
        padding: 20px 15px;
    }

    nav{        
        margin-top: 5px;
    }

    #menuToggle{
        display: none;
        position: relative;            
        z-index: 1;            
        -webkit-user-select: none;
        user-select: none;

        input{
            display: block;
            width: 40px;
            height: 32px;
            position: absolute;
            left: -5px;                
            cursor: pointer;                
            opacity: 0;
            z-index: 2;                
            -webkit-touch-callout: none;

            &:checked ~ span{
                opacity: 1;
                transform: rotate(45deg) translate(-2px, -1px);
            }

            &:checked ~ span:nth-last-child(3){
                opacity: 0;
                transform: rotate(0deg) scale(0.2, 0.2);
            }

            &:checked ~ span:nth-last-child(2){
                transform: rotate(-45deg) translate(0, -1px);
            }

            &:checked ~ div
            {
                transform: none;
            }
        }

        span{
            display: block;
            width: 33px;
            height: 4px;
            margin-bottom: 5px;
            position: relative;            
            background: #02569C;
            border-radius: 3px;                
            z-index: 1;                
            transform-origin: 4px 0px;                
            transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        opacity 0.55s ease;

            &:first-child{
                transform-origin: 0% 0%;
            }

            &:nth-last-child(2){
                transform-origin: 0% 100%;
            }
    
        }
    }

    #menu{
        position: absolute;
        width: 300px;
        margin: -100px 0 0 -50px;
        padding: 0 0 0px 50px;
        padding-top: 125px;            
        background: white;
        list-style-type: none;
        -webkit-font-smoothing: antialiased;
        transform-origin: 0% 0%;
        transform: translate(-100%, 0);            
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);        
        box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);

    }
}

.header__logo{
    height: 42px;
    width: auto;
    margin-right: 30px;
}

.header__search{
    display: flex;

    input{
       border: 0;
       border-radius: 50px;
       background: #F7FBFE;
       padding: 15px 25px;
        padding-right: 40px;

    }

    .search_search-btn{
        margin-left: -40px;
        margin-right: 40px;
        transform: translateY(2px);
        display: block;
        background: 0;
        border: 0;

    }
}

.header__navigation{
    display: flex;
    margin-right: 20px;

    li{
        a{
            padding: 10px 15px;
            font-size: 18px;
            transition: .3s;
            font-weight: 500;
            color: black;

            &:hover{
                color:  #02569C;
            }
        }
        
    }
}

.header__cart{
    display: flex;
    align-items: center;
    margin-right: 30px;
    margin-left: 15px;
    svg{
        margin-right: 5px;
    }
}

// 
// 
// 
//  PREV BLUE - #4174a5
// 
// 
// 

.header__profile-interract{
    
    button{         
        padding: 11px 22px;        
    }   
}

.interract_login{
    background: #02569C; 
    border: 2px solid #02569C;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #FFFFFF;
    transition: .3s;
    
    &:hover{
        opacity: 0.8;
    }
}

.interract_register{
    transition: .3s;

    &:hover{
        opacity: 0.9;
    }
}

.header__cource{
    margin-left: auto;
}

.header_profile{
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;

    .profile-name{
        font-size: 18px;
        text-align: center;
        color: #02569C;
        display: inline-block;
        margin-right: 20px;
        flex-shrink: 0;
    }

    .profile-image{
        width: 64px;
        height: 64px;
        border: 2px solid #02569C;
        border-radius: 50%;
        object-fit: cover;
        margin-right: 20px;
        flex-shrink: 0;
        transform: translateY(-2px);
    }
}

.main{
    padding: 0 15px;

    .wrapper{
        display: grid;
        grid-template-columns: 300px 1fr;
        column-gap: 50px;
        padding-bottom: 100px;

        sidebar{
            width: 100%;
            background: white;
            border-radius: 18px;
            padding: 30px 35px;
            padding-top: 50px;
            height: fit-content;
            box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);

            
        }

        .content{            
            width: 100%;
            background: white;
            border-radius: 18px;
            padding: 40px 50px;
            padding-top: 50px;
            box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);

            .item-block{
                display: flex;
                flex-direction: column;
                padding-bottom: 30px;
                margin-bottom: 40px;
                border-bottom: 1px solid #c4c4c4;

                .item-block_title{
                    font-weight: 700;
                    font-size: 32px;
                    line-height: 39px;
                    color: black;
                    margin-bottom: 50px;
                }

                .show-all{
                    display: block;
                    width:fit-content;
                    margin-left: auto;
                    border: 0;
                    background: 0;
                    text-decoration: underline;
                    color: #02569C;
                    transition: .3s;

                    &:hover{
                        opacity: 0.7;
                    }
                }

                &:last-child{
                    border-bottom: 0;
                    margin-bottom: -20px;
                }
            }

            .block-content{
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                column-gap: 40px;
                row-gap: 40px;
                margin-bottom: 15px;
            }
        }
    }

    .shop-item{
        border: 1px solid #F3F3F3;
        border-radius: 40px;
        padding: 25px 21px 34px 21px;
        box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);

        a{
            display: block;
        }

        .item_image{
            width: 100%;
            height:  171px;
            object-fit: cover;
            margin-bottom: 15px;
            border-radius: 16px;
        }

        .item_shop-name{
            font-size: 14px;
            line-height: 17px;
            text-decoration-line: underline;
            color: #02569C;
            margin-bottom: 15px;

            &.verified{
                position: relative;
                padding-left: 20px;

                &::before{
                    content: "";
                    background: url(../img/svg/verified-ico.svg);
                    background-size: cover;
                    position: absolute;
                    width: 16px;
                    height: 16px;
                    left: 0;
                    top: 1px;
                }
            }
        }

        .item_name{
            font-size: 14px;
            line-height: 17px;
            margin-bottom: 15px;
            word-break: break-all;
        }

        .item_info-group{
            margin-bottom: 15px;

            .item_sub-text{
                font-weight: 500;
                font-size: 11px;
                line-height: 13px;
                text-transform: uppercase;
                color: #BDBDBD; 
                margin-bottom: 7px;
            }

            .item_stars{
                display: flex;

                svg{
                    margin-right: 6px;
                }
            }

            &:last-child{
                margin-bottom: 0;
            }
        }

        .item_price{
            font-weight: 700;
            font-size: 16px;
            line-height: 17px;
        }
    }
}

.categories-title{
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 20px;
    color: #02569C;
}

.categories-list{

    li{
        padding: 25px 0;
        border-bottom: 1px solid #C4C4C4;

        a{
            display: flex;
            align-items: center;
            transition: color .3;

            &:hover{
                color: #02569C;
            }
        }
        

        img{
            margin-right: 15px;
        }

        p{
            font-size: 20px;
            line-height: 23px;
        }

        &:last-child{
            padding-bottom: 55px;
            border: 0;
        }
    }
}

footer{    
    width: 100vw;
    background: white;

    .wrapper{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 20px;
        padding: 50px 25px;

        h3{
            font-weight: 600;
            font-size: 22px;
            margin-bottom: 17px;
            color: #02569C;
        }

        ul{

            li{
                margin-bottom: 10px;

                a{
                    display: flex;

                }
                
            }
        }

        .col1{
            padding-right: 40px;
        }

        .footer_logo{
            margin-bottom: 20px;
        }
    }
}

@media screen and (max-width: 1300px) {

    .main{

        .wrapper{
            column-gap: 15px;

            .content{
                padding: 20px 30px;
                padding-top: 42px;

                .block-content{
                    column-gap: 15px;
                    row-gap: 25px;
                }
            }
        }
    }
}

@media screen and (max-width: 1150px) {

    #header{
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;

        #menuToggle{
            display: block;
        }

        .header__navigation, .header__cource,.header__profile-interract,.header__profile{
            display: none;
        }

        .wrapper{            
            padding: 20px 40px;

            nav{
                margin-right: 20px;
            }

            #menu{                
                height: 107vh;
            }

        }

        .menu-wrapper{
            overflow: scroll;
            padding-right: 50px;
            height: 100%;
        }

        .header__search{
            margin-left: auto;
        }

        .burger__navigation{
            margin-bottom: 50px;

            li{
                padding: 20px 0;                   
                border-bottom: 1px solid #C4C4C4;

                a{
                    font-size: 20px;
                    line-height: 23px;
                }

                &:last-child{
                    border-bottom: 0;
                }
            }           

        }

        .burger__profile-interract{
            display: flex;
            flex-direction: column;
            margin-bottom: 80px;
            
            button{                
                padding: 15px 10px; 
                margin-bottom: 5px;
            }
        }
    }    

    .main{
        margin-top: 140px;

        sidebar{
            display: none;
        }


        .wrapper{
            grid-template-columns: 1fr;
            max-width: 850px;

            .content{
                padding: 40px 50px;
                padding-top: 50px;

                .block-content{
                    column-gap: 20px;
                    row-gap: 40px;
                }
            }
        }
        
    }
}

@media screen and (max-width: 880px) {

    .main{
        margin-top: 140px;

        sidebar{
            display: none;
        }

        .wrapper{
            grid-template-columns: 1fr;
            max-width: 850px;

            .content{
                padding: 20px 30px;
                padding-top: 42px;

                .block-content{
                    column-gap: 15px;
                    row-gap: 25px;
                }

                .item-block_title{
                    margin-bottom: 30px;
                }
            }
        }
        
    }

    footer .wrapper {
        display: grid;
        grid-template-columns: 1.5fr 1fr 1fr;
    }
}

@media screen and (max-width: 780px) {
    .main{
        .wrapper{
            max-width: 450px;

            .content{
                padding: 40px 50px;
                padding-top: 50px;

                .block-content{
                    grid-template-columns: 1fr;
                }

                .item-block_title{
                    margin-bottom: 30px;
                }
            }
        }
    }
}

@media screen and (max-width: 660px) {

    .header__search {
        position: relative;
        width: 150px;
        
        .search_search-btn {
            margin: 0;
            right: 13px;
            top: 14px;
            position: absolute;
        }

        input{
            width: 100%;
        }
    }

    .header__logo{
        width: 100px;
        height: auto;
    }

    .header__cart{
        margin-right: 0;
    }
    

    footer{
        padding: 0 15px;

        .wrapper{
            max-width: 400px;
            grid-template-columns: 1fr;
            row-gap: 40px;
        }
    }
}

@media screen and (max-width: 450px) {

    #header{
        .wrapper{
            padding: 20px 20px;
        }
    }

    .header__logo{
        margin-right: 8px;       
    }

    .header__cart{
        margin-right: 0;
    }
    
    .main .wrapper .content {
        padding: 20px 30px;
        padding-top: 42px;

    }

}